import React, { Component } from 'react';
import './App.css';

class App extends Component {
  state = {
    // query:'',
    // weather:{}
    city: "",
    result: ""
  }
  render() {
    const api = {
      key: "0f529f983b3d62b2549420efe1063278"
    }
    const dateBuilder = (d) => {
      let months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
      let days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thusday", "Friday", "Saturday"];

      let day = days[d.getDay()];
      let date = d.getDate();
      let month = months[d.getMonth()];
      let year = d.getFullYear();

      return `${day} ${date} ${month} ${year}`
    }
    const search = evt => {
      if (evt.key === 'Enter') {
        fetch(`https://api.openweathermap.org/data/2.5/weather?q=${this.state.city}&appid=${api.key}`)
          .then(Response => Response.json())
          .then(result => this.setState({ result: result }))
      }
    }

    return (
      <div className={(this.state.result.main !== undefined) 
                      ? (this.state.result.main.temp > 290) 
                        ?'app worm' 
                         : 'app'
                      :'app'}>
        <main>
          <div className='search-box'>
            <input
              type="text"
              placeholder='Search City, State or Country...and Press Enter'
              className="search-bar"
              onChange={e => this.setState({ city: e.target.value })}
              value={this.state.city}
              onKeyPress={search}
            />
          </div>
          {(typeof this.state.result.main != "undefined") ? (
            <div className='location-box'>
              <div className='location'>{this.state.result.name} {this.state.result.sys.country}</div>
              <div className='date'>{dateBuilder(new Date())}</div>
              <div className="weather-box">
                <div className='temp'>{Math.round(this.state.result.main.temp)}Â°C</div>
                <div className="weather">{this.state.result.weather[0].main}</div>
              </div>
            </div>
          ) : ('')}
        </main>
      </div>
    );
  }
}
export default App;
